<template>
  <div class="operations" v-if="operations.length>0 && imgscurrent.length>0">

    <div v-for="(item,index) in operations">
      <!--运营位类别:优惠券-->
      <div v-if="item.type === 1">
        <!--优惠券样式类型、优惠券状态-->
        <img @click.prevent="" v-if="item.coupon.status === 0" :src="imgscurrent[index].img_receive"
             @click="consumer(index,item.coupon.id,item.coin_price.coin_price_id,item.coupon.type,item.coin_price.coin_num,item.coin_price.type)" id="free_doll"/>
        <img @click.prevent="" v-else-if="item.coupon.status === 1 && item.coupon.type !==2"
             :src="imgscurrent[index].img_use"
             @click="useCoupons(item.coupon.id,item.coin_price.coin_price_id,item.coupon.type,item.coin_price.coin_num,item.coin_price.type)"/>
        <img @click.prevent="" v-else="" :src="imgscurrent[index].img_already_use"
             @click="useInfinite(item.coin_price.type)">

        <!--优惠券最左边文字-->
        <!--<span v-if="item.coupon.type === 2 && item.coin_price.type === 0" class="quan_game_num">{{item.coin_price.coin_num/info.coin_num}}次</span>-->
        <span v-if="item.coupon.type !== 2 && item.coin_price.type === 0" class="quan_game_num">优惠</span>
        <span v-if="item.coin_price.type === 1" class="quan_game_num">无限</span>
        <!--优惠券最左边文字-->

        <!--优惠券类型不是免费玩的打折前的价格-->
        <span style="pointer-events: none;" v-if="item.coupon.type !== 2"
              class="cost-price">{{item.coin_price.coin_price}}元</span>
        <!--优惠券类型不是免费玩的打折前的价格-->

        <!--打折后的价格根据优惠券类型计算价格：普通模式-->
        <span style="pointer-events: none;" v-if="item.coin_price.type === 0 && item.coupon.type===1"
              class="put-tip">{{(item.coin_price.coin_price - item.coupon.reduce).toFixed(2) | handleNum}}元</span>
        <span style="pointer-events: none;" v-if="item.coin_price.type === 0 && item.coupon.type===0 " class="put-tip">{{(item.coin_price.coin_price * item.coupon.reduce / 100).toFixed(2) | handleNum}}元</span>
        <!--打折后的价格根据优惠券类型计算价格：普通模式-->

        <!--中间的提示：若优惠券绑定的充值项为无限玩就会显示价格-->
        <p class="infinity-tip" style="pointer-events: none;" v-if="item.coupon.type !== 2">
          <span
            v-if="item.coin_price.type === 1 && item.coupon.type===1">{{(item.coin_price.coin_price - item.coupon.reduce).toFixed(2) | handleNum}}元</span><span
          v-if="item.coin_price.type === 1 && item.coupon.type===0">{{(item.coin_price.coin_price * item.coupon.reduce / 100).toFixed(2) | handleNum}}元</span><span>{{item.coupon.title}}</span>
        </p>
        <!--中间的提示：若优惠券绑定的充值项为无限玩就会显示价格-->

        <!--优惠券如果是已经使用的状态的章子图片-->
        <img @click.prevent="" v-if="item.coupon.status === 2 && item.coupon.type !== 2" class="used"
             src="http://res.catchme.com.cn/imgs-2017-12-29-20-42/used.png" alt="">
        <!--优惠券如果是已经使用的状态的章子图片-->
      </div>
      <!--运营位类别:红包-->
      <div v-if="item.type === 4" @click="consumerRed(item)" id="luck_draw">
        <img class="price" v-if="item.timeType === 'received' && item.coin_price.coin_num === 10" :src="price.num10" alt="">
        <img class="price" v-if="item.timeType === 'received' && item.coin_price.coin_num === 12" :src="price.num12" alt="">
        <img class="price" v-if="item.timeType === 'received' && item.coin_price.coin_num === 88" :src="price.num88" alt="">
        <img class="price" v-if="item.timeType === 'used' && item.coin_price.coin_num === 10" :src="price.num10_used" alt="">
        <img class="price" v-if="item.timeType === 'used' && item.coin_price.coin_num === 12" :src="price.num12_used" alt="">
        <img class="price" v-if="item.timeType === 'used' && item.coin_price.coin_num === 88" :src="price.num88_used" alt="">
        <img @click.prevent="" v-if="item.timeType === 'unreceived'" :src="time.unreceived" alt="">
        <img @click.prevent="" v-if="item.timeType === 'received'" :src="time.received" alt="">
        <img @click.prevent="" v-if="item.timeType === 'notPrize'" :src="time.notPrize" alt="">
        <img @click.prevent="" v-if="item.timeType === 'used'" :src="time.used" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  import CONFIG from '../config'
  import {mapState} from 'vuex'

  export default {
    name: 'operations',
    mounted() {
        if(this.version2){
          this.imgs = this.imgs2;
          this.time = this.time2;
        }
//      需要根据优惠券的样式类型判断
      this.$store.dispatch('getOperations').then((res) => {
        //还会有一个类型的运营位，作用如果该运营位包含该充值项的话，当点击充值项时，直接使用优惠券去付款
        for (var i = 0; i < res.length; i++) {
          if (res[i].type === 1) {
            //根据优惠券样式来获取背景图片
            //充值项类型为无限玩单独处理
            if(res[i].coupon.type===2 && res[i].coupon.status === 1){
              var coin = res[i].coin_price.coin_num;
              this.$store.dispatch('getFreeCoin', {coin_price_id: res[i].coin_price.coin_price_id, coupon_id: res[i].coupon.id}).then(() => {
                this.$store.commit('setCoins', coin);
                this.$store.dispatch('getOperations')
              });
            }
            if (res[i].coin_price.type === 1) {
              this.imgscurrent.push(this.imgs.infinity);
            } else {
              switch (res[i].coupon.style) {
                case 1:
                  this.imgscurrent.push(this.imgs.newUser);
                  break; //首单立减
                case 2:
                  this.imgscurrent.push(this.imgs.free);
                  break; //免费玩一次
                case 3:
                  this.imgscurrent.push(this.imgs.infinity);
                  break; //无限玩
                default:
                  this.imgscurrent.push(this.imgs.newUser);
                  break; //默认
              }
            }
          } else {
            this.imgscurrent.push({});
          }
        }
      })
    },
    data() {
      return {
        imgs: {
          free: {
//            img_receive: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_free_un1.png',
//            img_use: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_free1.png',
//            img_already_use:'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_free_used1.png'
            img_use:'http://res.catchme.com.cn/imgs-2018-02-05/free_useit_now.png',
            img_receive: 'http://res.catchme.com.cn/imgs-2018-02-05/free_Receive.png',
            img_already_use: 'http://res.catchme.com.cn/imgs-2018-02-05/free_used.png'
          },
          infinity: {
            img_receive: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_201.png',
            img_use: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_201_use1.png',
            img_already_use: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_card_20_used1.png'
          },
          newUser: {
            img_receive: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_new_un1.png',
            img_use: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_new_use1.png',
            img_already_use: 'http://res.catchme.com.cn/imgs-2017-12-29-20-42/quan_new_used1.png'
          }
        },
        time: {
          'unreceived': 'http://res.catchme.com.cn/imgs-2018-02-05/red1.png',
          'received': 'http://res.catchme.com.cn/imgs-2018-02-05/redd3.png',
          'used':'http://res.catchme.com.cn/imgs-2018-02-05/redd2.png',
          'notPrize':'http://res.catchme.com.cn/imgs-2018-02-05/not-prize.png'
        },
        price:{
          'num10':'http://res.catchme.com.cn/imgs-2018-02-05/num10.png',
          'num12':'http://res.catchme.com.cn/imgs-2018-02-05/num12.png',
          'num88':'http://res.catchme.com.cn/imgs-2018-02-05/num88.png',
          'num10_used':'http://res.catchme.com.cn/imgs-2018-02-05/num10-2.png',
          'num12_used':'http://res.catchme.com.cn/imgs-2018-02-05/num12-2.png',
          'num88_used':'http://res.catchme.com.cn/imgs-2018-02-05/num88-2.png'
        },
        imgs2:{
          free: {
            img_use:'http://res.catchme.com.cn/imgs-2018-02-05/free_useit_now-2.png',
            img_receive: 'http://res.catchme.com.cn/imgs-2018-02-05/free_Receive-2.png',
            img_already_use: 'http://res.catchme.com.cn/imgs-2018-02-05/free_used-2.png'
          },
        },
        time2: {
          'unreceived': 'http://res.catchme.com.cn/imgs-2018-02-05/red1-2.png',
          'received': 'http://res.catchme.com.cn/imgs-2018-02-05/redd3-2.png',
          'used':'http://res.catchme.com.cn/imgs-2018-02-05/redd2-2.png',
          'notPrize':'http://res.catchme.com.cn/imgs-2018-02-05/not-prize2.png'
        },
        imgscurrent: []
      }
    },
    computed: mapState({
      user: state => state.user.user,
      info: state => state.user.info,
      operations: state => state.user.operations
    }),
    methods: {
      consumerRed(item) {
        if (item.timeType === 'unreceived') {
          _hmt.push(['_trackEvent', '新春抽大奖', '点击', '', '']);
          this.$store.dispatch('redReceive', item.record_id);
          this.$store.dispatch('getOperations');
          if (item.coupon.id === -1) {
            this.$emit('changeBgShow', {bgShow: true, contentShow: 'unRedCoupon'});
          } else {
            this.$emit('changeBgShow', {bgShow: true, contentShow: 'redCoupon', currentCoupon: item});
          }
        } else if (item.timeType === 'received') {
          _hmt.push(['_trackEvent', '直接使用红包', '点击', '红包价格为'+(item.coin_price.coin_price-item.coupon.reduce)+'元', '']);
          //调用充值
          this.useCoupons(item.coupon.id, item.coin_price.coin_price_id, item.coupon.type, item.coin_price.coin_num, item.coin_price.type,item.coin_price.coin_price,item.coupon.reduce);
        }
      },
      //领取优惠券
      consumer(index, coupon_id, coin_price_id, coupon_type, coin, type) {
        this.$store.dispatch('consumer', {coupon_id: coupon_id}).then(() => {
          _hmt.push(['_trackEvent', '领取抓娃娃优惠券', '点击', '免费抓娃娃优惠券', '']);
          if (coupon_type === 2) {
            this.useCoupons(coupon_id, coin_price_id, coupon_type, coin, type);
          } else if (type === 1) {
            this.$emit('changeBgShow', {
              bgShow: true,
              contentShow: 'infinityTip',
              currentCoupon: this.operations[index]
            });
          } else {
            this.$emit('changeBgShow', {bgShow: true, contentShow: 'newUser', currentCoupon: this.operations[index]});
          }
          if (coupon_type !== 2) {
            this.operations[index].coupon.status = 1;
          }
        })
      },
      useCoupons(coupon_id, coin_price_id, coupon_type, coin, type,coin_price,reduce) {
        var actualPrice;
        if(coupon_type ===4){
          actualPrice = coin_price * reduce / 100;
        }else if(coupon_type === 3){
          actualPrice = coin_price - reduce;
        }
        if (coupon_type === 2 || actualPrice === 0) {
          this.$store.dispatch('getFreeCoin', {coin_price_id: coin_price_id, coupon_id: coupon_id}).then((data) => {
            this.$store.commit('setCoins', coin);
            this.$store.dispatch('getOperations')
            this.$emit('closeBg');
          });
        } else {
          var self = this;
          if (CONFIG.isAlipay) {
            this.$store.dispatch('getPayAlipay', {coin_price_id: coin_price_id, coupon_id: coupon_id}).then((data) => {
              AlipayJSBridge.call("tradePay", {
                tradeNO: data.data.trade_no
              }, function (result) {
                if (result.resultCode === '9000') {
                  if (type !== 1) {
                    self.$store.commit('setCoins', coin);
                    self.$emit('closeBg');
                  } else {
                    //type为1是无限模式
                    self.$store.dispatch('InfiniteGame').then(() => {
                      self.$emit('changeTip');
                    });
                  }
                  self.$store.dispatch('getOperations')
                }
              });
            })
          } else if (CONFIG.isWx) {
            this.$store.dispatch('getPayJssdk', {coin_price_id: coin_price_id, coupon_id: coupon_id})
              .then((data) => {
                if (data.status_code === 200) {
                  const d = data.data
                  wx.chooseWXPay({
                    timestamp: d.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    nonceStr: d.nonceStr, // 支付签名随机串，不长于 32 位
                    package: d.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                    signType: d.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    paySign: d.paySign, // 支付签名
                    success: function (res) {
                      if (type !== 1) {
                        self.$store.commit('setCoins', coin);
                        self.$emit('closeBg');
                      } else {
                        self.$store.dispatch('InfiniteGame').then(() => {
                          self.$emit('changeTip');
                        });
                      }
                      self.$store.dispatch('getOperations')
                    }
                  })
                }
              })
          }
        }
      },
      useInfinite(type) {
        if (type === 1 && this.user.coin_infinite > 0) {
          this.$store.dispatch('InfiniteGame');
        }
      },
    },

  }
</script>

<style>
  .operations > div .price{
    width: 2.26rem;
    position: absolute;
    left: 50%;
    top:0.3rem;
    transform: translateX(-50%);
  }
  .operations {
    width: 100%;
    /*height: 100%;*/
    /*height: 1.78rem;*/
    height: 100%;
  }
  .operations > div img {
    width: 3.42rem;
    /*max-height: 1.78rem;*/
    /*height: 2rem;*/
    /*height: 100%;*/
    display: block;
    float: left;
  }

  .operations > div .used {
    width: 1.81rem;
    position: absolute;
    right: -0.4rem;
    top: 0.6rem;
    z-index: 3;
  }

  .operations > div .infinity-tip {
    color: #fff;
    position: absolute;
    left: 1.24rem;
    top: 0.7rem;
    font-size: 0.34rem;
    line-height: 0.34rem;
  }

  .operations > div .put-tip {
    color: #fff;
    position: absolute;
    left: 1.24rem;
    top: 0.26rem;
    font-size: 0.34rem;
    line-height: 0.34rem;
  }

  .operations > div .cost-price {
    color: #fff;
    text-decoration: line-through;
    font-size: 0.34rem;
    line-height: 0.34rem;
    position: absolute;
    /*right: 0.3rem;*/
    right: 0.12rem;
    top: 0.26rem;
  }

  .operations > div .quan_game_num {
    position: absolute;
    left: 0.37rem;
    top: 0.68rem;
    width: 0.5rem;
    text-align: center;
    color: #fff;
  }

  .operations > div:first-of-type > div {
    float: left !important;
  }

  .operations > div > div {
    float: left;
    position: relative;
    overflow: hidden;
  }

  .operations > div:last-of-type > div {
    float: right;
  }

  .operations .ptip {
    position: absolute;
    color: #ff4848;
    font-size: 0.5rem;
    left: 0;
    top: 0.15rem;
    width: 3.42rem;
    text-align: center;
    font-weight: 600
  }

</style>



