<script>
  import CONFIG from '../config'
  import {mapState} from 'vuex'
  import joPay from './wxpay'
  import operations from './operations.vue'
  import {MessageBox} from 'mint-ui'

  export default {
    data() {
      return {
        start_desc: '投币启动',
        gameNum: 1,
        is_start: false,
        is_lamp_after: false,
        is_recharge: false,
        loading_art: '加载更多~',
        load: false,
        max_page: false,
        maskShow: false,
        isShow: '',
        showHtml: false,
        bgShow: false,
        contentShow: '',
        currentCoupon: {},
        pay: {},
        currentCouponPay: {},
      }
    },
    created() {
      this.Indicator.open()
    },
    computed: mapState({
      user: state => state.user.user,
      consume: state => state.user.consume,
      machine_no: state => state.user.machine_no,
      info: state => state.user.info,
      hide_coupons: state => state.user.hide_coupons
    }),
    components: {
      joPay,
      operations
    },
    mounted() {
      this.$store.dispatch('judgeMachine')
      this.$store.dispatch('getUser')
        .then(() => {
          this.Indicator.close()
          this.showHtml = true
        })
    },
    methods: {
      useCouponPay() {
        if (this.currentCouponPay.coin_price === "9.00") {
          _hmt.push(['_trackEvent', '使用9元10币隐藏优惠券', '点击', '', '']);
        } else if (this.currentCouponPay.coin_price === "19.00") {
          _hmt.push(['_trackEvent', '使用19元22币隐藏优惠券', '点击', '', '']);
        }
        this.$refs.joPay.handlePay(this.currentCouponPay.coin_price_id, this.currentCouponPay.coin_num, this.currentCouponPay.status, this.currentCouponPay.coin_price, this.currentCouponPay.type);
      },
      continueRecharge() {
        if (this.pay.coin_price === "2.00") {
          _hmt.push(['_trackEvent', '继续充值2元', '点击', '', '']);
        } else if (this.currentCouponPay.coin_price === "10.00") {
          _hmt.push(['_trackEvent', '继续充值10元', '点击', '', '']);
        }
        this.$refs.joPay.handlePay(this.pay.coin_price_id, this.pay.coin_num, this.pay.status, this.pay.coin_price, this.pay.type);
      },
      changeBgShow(obj) {
        this.bgShow = obj.bgShow;
        this.contentShow = obj.contentShow;
        this.currentCoupon = obj.currentCoupon;
        this.pay = obj.pay;
        this.currentCouponPay = obj.currentCouponPay;
      },
      changeTip() {
        this.bgShow = true;
        this.contentShow = 'infinity';
        this.is_lamp_after = true
        setTimeout(() => {
          this.is_lamp_after = false
        }, 60000)
      },
      closeBg(value) {
        if (value === 'red') {
          _hmt.push(['_trackEvent', '关闭红包弹窗', '点击', '', '']);
        } else if (value === 'hide') {
          if (this.pay.coin_price === "2.00") {
            _hmt.push(['_trackEvent', '关闭2元弹窗优惠券', '点击', '', '']);
          } else if (this.pay.coin_price === "10.00") {
            _hmt.push(['_trackEvent', '关闭10元弹窗优惠券', '点击', '', '']);
          }
        }
        this.bgShow = false;
      },
      useCoupons(coupon_id, coin_price_id, coupon_type, coin, type, coin_price, reduce) {
        _hmt.push(['_trackEvent', '弹窗按钮使用红包', '点击', '红包价格为' + (coin_price-reduce) + '元', '']);
        this.$refs.operations.useCoupons(coupon_id, coin_price_id, coupon_type, coin, type, coin_price, reduce);
      },
      handlerGameNum(x) {
        if (x === '-') {
          if (this.gameNum === 1) return false;
          this.gameNum--;
          _hmt.push(['_trackEvent', '减币', '点击', '', '']);
        } else {
          if (((this.gameNum + 1) * this.info.coin_num) > (this.user.coins + this.info.coin_num * this.user.game_ticket)) {
            if (this.user.coins < this.info.coin_num) this.openRecharge('recharge')
            return false
          }
          this.gameNum++;
          _hmt.push(['_trackEvent', '加币', '点击', '', '']);
        }
      },
      //投币，开始游戏
      handleStartingDevice() {
        this.closeBg();
        if (this.info.online == 0) {
          MessageBox.alert('设备离线请扫码更换机器', '提示').then(() => {
            this.handleScanQRCode();
          });
          return
        }
        if (this.is_start) {
          return
        } else {
          this.is_start = true
          this.is_lamp_after = false
          this.start_desc = '投币中'
          if (this.user.game_ticket + this.user.coins < this.info.coin_num) {
            this.start_desc = '';
            this.is_start = false
            MessageBox.alert('余额不足，请充值', '提示').then(() => {
            });
            return false
          }
          //添加百度统计
          _hmt.push(['_trackEvent', '主按钮投币', '点击', '投币-游戏次数-' + this.gameNum, '']);
          this.$store.dispatch('startingDevice', this.gameNum * this.info.coin_num)
            .then(() => {
              this.is_lamp_after = true
              this.is_start = false
              this.start_desc = '投币启动'
              this.gameNum = 1;
              setTimeout(() => {
                this.is_lamp_after = false
              }, 12000 * this.gameNum)
            })
            .catch(() => {
              this.is_start = false
              this.start_desc = '投币启动'
            })
        }
      },
      handleScanQRCode() {
        _hmt.push(['_trackEvent', '扫码更换机器', '点击', '', '']);//添加百度统计
        if (CONFIG.isAlipay) {
          ap.scan(function (res) {
            window.location.href = res.code
          })
        } else {
          wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
              window.location.href = result
            },
            error: function () {
              alert('扫码失败')
            }
          })
        }
      },
      support() {
        _hmt.push(['_trackEvent', '客服', '点击', '', '']);
      },
    },

  }
</script>

