<template>
  <div class="recharge-lists clearfix"  :class="{'version2':version2}" id="recharge">
    <div v-for="v in coin" :data-id="v.coin_price_id"
         @click="handlePayBefore(v)" :class="{'active':v.status==0,'infinity':v.type==1}">
      <div class="recharge-item-t"><span :class="{'twoCoin':v.coin_num==2,'infinite':v.type==1}"></span><i
        v-if="v.type==1">无限币</i><i v-else>{{v.coin_num}}币</i></div>
      <div class="recharge-item-b">{{v.coin_price}}元<span
        v-if="v.sale_state == 1 && v.original_price">({{v.original_price}}元)</span></div>
      <!--<div class="recharge-hot hot-limit" v-if="v.coin_buy_state==1 && v.status != 0">限购{{v.coin_buy_num}}次</div>-->
      <!--<div class="recharge-hot hot-limit">限购5次</div>-->
      <!--<div class="recharge-hot hot-top" v-if="v.remarks != null && v.remarks != '' && v.status != 0">{{v.remarks}}</div>-->
      <!--<div class="recharge-has" v-show="v.status==0">领取过啦</div>-->
    </div>
  </div>
</template>

<script>
  import {instance} from '../config/common'
  import {mapState} from 'vuex'
  import CONFIG from '../config'
  import {SetCookie} from '../util'
  import {MessageBox} from 'mint-ui'

  export default {
    data() {
      return {
        isActive: false,
        isRequest: false,
      }
    },
    computed: mapState({
      user: state => state.user.user,
      coin: state => state.user.coin,
      machine_no: state => state.user.machine_no,
      info: state => state.user.info,
      hide_coupons: state => state.user.hide_coupons
    }),
    mounted() {
      this.getCoinList()
    },
    methods: {
      getCoinList() {
        this.$store.dispatch('getCoinList')
      },
      handlePayBefore(pay) {
        if (pay.status === 0) {
          return false;
        }
        var hideArr = JSON.parse(localStorage.getItem('hide_coupons')) || [];
        var date = new Date();
        var time = '' + date.getFullYear() + (date.getMonth() + 1) + date.getDate();
        if (hideArr.length > 0 && hideArr.indexOf(JSON.stringify({time: time, id: pay.coin_price_id})) !== -1) {
          this.handlePay(pay.coin_price_id, pay.coin_num, pay.status, pay.coin_price, pay.type);
        } else {
          if (hideArr.length > 0) {
            for (var j = 0; j < hideArr.length; j++) {
              if (JSON.parse(hideArr[j]).id === pay.coin_price_id) {
                hideArr.splice(j, 1);
                break;
              }
            }
          }
          hideArr.push(JSON.stringify({time: time, id: pay.coin_price_id}));
          localStorage.setItem('hide_coupons', JSON.stringify(hideArr));
          //在这里得到充值项的优惠券
          var currentCouponPay;
          var len = this.hide_coupons.length;
          for (var i = 0; i < len; i++) {
            if (this.hide_coupons[i].after_coin_price_id) {
              if (this.hide_coupons[i].after_coin_price_id.indexOf(pay.coin_price_id) !== -1) {
                currentCouponPay = this.hide_coupons[i].coin_price;
                break;
              }
            }
          }
          if (currentCouponPay) {
              if (pay.coin_price === "2.00") {
                _hmt.push(['_trackEvent', '弹出2元下面优惠券', '点击', '', '']);
              } else if (pay.coin_price === "10.00") {
                _hmt.push(['_trackEvent', '弹出10元下面优惠券', '点击', '', '']);
              }
            this.$emit('changeBgShow', {
              bgShow: true,
              contentShow: 'hideCoupon',
              pay: pay,
              currentCouponPay: currentCouponPay
            });
          } else {
            this.handlePay(pay.coin_price_id, pay.coin_num, pay.status, pay.coin_price, pay.type);
          }
        }
      },
      handlePay(id, coin, status, price, type) {
        if (!this.isRequest) {
          this.isRequest = true;
          if (this.info.online == 0) {
            MessageBox.alert('设备离线请扫码更换机器', '提示').then(() => {
              this.$emit('handleScanQRCode');
            });
            return
          }
          let self = this
          //status为0不可购买
          if (status === 0) {
            return false;
          }
          if (price == '0.00') {
            //免费请求
            this.$store.dispatch('getFreeCoin', {coin_price_id: id}).then((data) => {
              this.isRequest = false;
              if (data.status_code == 200) {
                this.$store.dispatch('getCoinList').then(() => {
                  this.$store.dispatch('getUser');
                });
              }
            }).catch(() => {
              this.isRequest = false;
            });
          } else if (type == 1 && this.user.coin_infinite > 0) {
            this.$store.dispatch('InfiniteGame').then(() => {
              this.$emit('alertTip');
              this.isRequest = false;
            }).catch(() => {
              this.isRequest = false;
            });
          } else {
            if (CONFIG.isAlipay || CONFIG.isTpp) {
              this.$store.dispatch('getPayAlipay', {coin_price_id: id}).then((data) => {
                self.isRequest = false;
                AlipayJSBridge.call("tradePay", {
                  tradeNO: data.data.trade_no
                }, function (result) {
                  if (result.resultCode == '9000') {
                    if (type !== 1) {
                      self.$store.commit('setCoins', coin)
                      self.getCoinList()
                      self.$emit('closeBg');
                    } else {
                      self.$store.dispatch('InfiniteGame').then(() => {
                        self.$emit('alertTip');
                      })
                    }
                  }
                });
              }).catch(() => {
                self.isRequest = false;
              })
            } else if (CONFIG.isWx) {
              this.$store.dispatch('getPayJssdk', {coin_price_id: id})
                .then((data) => {
                  self.isRequest = false;
                  if (data.status_code === 200) {
                    const d = data.data
                    wx.chooseWXPay({
                      timestamp: d.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                      nonceStr: d.nonceStr, // 支付签名随机串，不长于 32 位
                      package: d.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                      signType: d.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                      paySign: d.paySign, // 支付签名
                      success: function (res) {
                        if (type !== 1) {
                          self.$store.commit('setCoins', coin)
                          self.$emit('closeBg');
                        } else {
                          self.$store.dispatch('InfiniteGame').then(() => {
                            self.$emit('alertTip');
                          })
                        }
                        self.getCoinList()
                      }
                    })
                  }
                }).catch(() => {
                self.isRequest = false;
              })
            }
          }
        }
      }

    }
  }
</script>

